<script setup lang="ts">
import { computed, toRefs } from 'vue'
import { useVModel } from '@vueuse/core'
import { DatePicker } from 'v-calendar'
import { PopoverPanel } from '@headlessui/vue'
import { addDays, isSameDay, startOfToday, subDays } from 'date-fns'
import { CalendarDaysIcon, ListBulletIcon } from '@heroicons/vue/24/solid'

import 'v-calendar/style.css'

import { formatRelativeDay } from '@/shared/lib/date'
import { type ClassValue, cn } from '@/shared/lib/style'
import { PPopover, PPopoverButton } from '@/shared/ui/PPopover'

import { DEFAULT_DAYS_COUNT } from './config'
import type { PDaysModel } from './types'
import PDay from './PDay.vue'

export interface Props {
  class?: ClassValue
  modelValue?: PDaysModel
  days?: number
  disabledDates?: Date[]
  reverse?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  modelValue: null,
  days: DEFAULT_DAYS_COUNT,
})

const { days, reverse } = toRefs(props)

export type Emits = {
  'update:modelValue': [value: Date]
}

const emit = defineEmits<Emits>()

const vModel = useVModel(props, 'modelValue', emit)

const renderedDays = computed(() => {
  const today = startOfToday()

  const daysArray = [today]

  for (let i = 1; i <= days.value; i++) {
    daysArray.push((reverse.value ? subDays : addDays)(today, i))
  }

  return daysArray
})

const isCustomDate = computed(() => {
  const value = vModel.value

  return !!value && !(value instanceof Date)
})

const datePickerModel = computed(({
  get: () => vModel.value instanceof Date
    ? { start: vModel.value, end: vModel.value }
    : vModel.value,
  set: (value) => vModel.value = value,
}))
</script>

<template>
  <div
    :class="cn(
      'flex gap-4',
      reverse && 'flex-row-reverse',
      props.class,
    )"
  >
    <PDay :selected="!vModel" @click="vModel = null">
      <ListBulletIcon class="size-5" />
    </PDay>

    <PDay
      v-for="day in renderedDays"
      :key="day.toISOString()"
      :date="day"
      :selected="vModel instanceof Date && isSameDay(day, vModel)"
      :disabled="disabledDates && disabledDates.some((date) => isSameDay(date, day))"
      @click="vModel = day"
    >
      {{ formatRelativeDay(day) }}
    </PDay>

    <PPopover v-slot="{ close }">
      <PPopoverButton>
        <PDay :selected="isCustomDate">
          <CalendarDaysIcon class="size-5" />
        </PDay>
      </PPopoverButton>

      <PopoverPanel>
        <DatePicker
          v-model.range="datePickerModel"
          @update:model-value="(event) => {
            datePickerModel = event
            close()
          }"
        />
      </PopoverPanel>
    </PPopover>
  </div>
</template>
